%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@-webkit-keyframes secBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@keyframes secBlock{0%{width:0%;left:0}50%{width:100%;left:0}100%{width:0;left:100%}}@-webkit-keyframes mainFadeIn{0%{opacity:0}100%{opacity:1}}@keyframes mainFadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes popIn{0%{width:0px;height:0px;background:#e9d856;border:0px solid #ddd;opacity:0}50%{width:10px;height:10px;background:#e9d856;opacity:1;bottom:45px}65%{width:7px;height:7px;bottom:0px;width:15px}80%{width:10px;height:10px;bottom:20px}100%{width:7px;height:7px;background:#e9d856;border:0px solid #222;bottom:13px}}@keyframes popIn{0%{width:0px;height:0px;background:#e9d856;border:0px solid #ddd;opacity:0}50%{width:10px;height:10px;background:#e9d856;opacity:1;bottom:45px}65%{width:7px;height:7px;bottom:0px;width:15px}80%{width:10px;height:10px;bottom:20px}100%{width:7px;height:7px;background:#e9d856;border:0px solid #222;bottom:13px}}@-webkit-keyframes secFadeIn{0%{opacity:0}100%{opacity:0.5}}@keyframes secFadeIn{0%{opacity:0}100%{opacity:0.5}}@-webkit-keyframes top{0%{opacity:0;bottom:-80px}100%{opacity:1;bottom:0px}}@keyframes top{0%{opacity:0;bottom:-80px}100%{opacity:1;bottom:0px}}@-webkit-keyframes icon{0%{opacity:0;transform:scale(0)}50%{opacity:1;transform:scale(1.3) rotate(-2deg)}100%{opacity:1;bottom:0px}}@keyframes icon{0%{opacity:0;transform:scale(0)}50%{opacity:1;transform:scale(1.3) rotate(-2deg)}100%{opacity:1;bottom:0px}}

